.walletWrap{

}
.titleWrapBlock{
    display: flex;
    flex-direction: column;
    margin: 35px 0 37px 0;
}
.titleWrap{
    display: flex;
    align-items: center;
    margin: 0;
    font-family: 'Proxima Nova', sans-serif;
    font-style: normal;
    font-weight: 600;
    font-size: 23px;
    line-height: 28px;
    color: #000000;
    button{
        display: flex;
        border:0;
        background-color: #FFFFFF;
        width: 19px;
        padding: 0;
        img{
            width: 10px;
            height: 14px;
            transform: rotate(90deg);
        }
    }
}
.title{
    margin: 0;
}
.titleDescription{
    margin: 6px 0 0 0;
    font-family: 'Proxima Nova',sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 15px;
    line-height: 18px;
    color: #000000;
}
.buttonWrap{
    margin: 30px 0 72px 0;
    height: 60px;
}
.warningBlock{
    box-sizing: border-box;
    width: 351px;
    height: 164px;
    padding: 18px 22px 22px 25px;
    background: #FBEFED;
    border-radius: 15px;
    margin-bottom: 23px;
}
.warningTitle{
    margin: 0;
    font-family: 'Proxima Nova', sans-serif;
    font-style: normal;
    font-weight: 600;
    font-size: 15px;
    line-height: 18px;
    color: #D4604B;
}
.warningDescription{
    padding-left: 25px;
    margin: 19px 0 22px 0;
    font-family: 'Proxima Nova',sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 15px;
    line-height: 18px;
    color: #000000;
}