.card{
    display: inline-block;
    border: 1px solid  #F3F4F5;
    border-radius: 10px;
    padding: 15px;
    margin: 15px 15px 15px 0;
}
.vacancyTitle{
    margin: 0;
    font-family: 'Proxima Nova', sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
}
.vacancyDescription{
    margin: 0;
    font-family: 'Proxima Nova', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 15px;
    line-height: 20px;
}