.summaryWrap{
    width: 351px;
}
.summary{
    height: 54px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    background: #F3F4F5;
    border-radius: 15px;
    p{
        font-family: 'Proxima Nova', sans-serif;
        font-style: normal;
        font-weight: 400;
        font-size: 16px;
        line-height: 19px;
        color: #000000;
        span{
            font-weight: 600;
            color: #000000;
        }
    }
}
.summaryTitleBlock{
    display: flex;
    justify-content: space-between;
    p{
        margin: 0 0 12px 0;
    }
}
.title{
    font-family: 'Proxima Nova',  sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 15px;
    line-height: 18px;
    color: #000000;
}
.priceRenewal{
    display: flex;
    justify-content: center;
    align-items: start;
    font-family: 'Proxima Nova', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 15px;
    line-height: 18px;
    color: #505052;
    img{
        margin: 3px 9px 0 0;
    }
}
.counterWrap{
    display: flex;

}