.titleWrapBlock{
    display: flex;
    margin: 35px 0 30px 0;
}
.titleWrap{
    display: flex;
    align-items: center;
    margin: 0;
    font-family: 'Proxima Nova', sans-serif;
    font-style: normal;
    font-weight: 600;
    font-size: 23px;
    line-height: 28px;
    color: #000000;
    button{
        display: flex;
        border:0;
        background-color: #FFFFFF;
        width: 19px;
        padding: 0;
        img{
            width: 10px;
            height: 14px;
            transform: rotate(90deg);
        }
    }
}
.buttonWrap{
    height: 60px;
}